.nav-link {
  color: var(--#{$variable-prefix}danger);
  &:hover,
  &:focus {
    color: var(--#{$variable-prefix}danger);
  }
}
.nav-tabs {
  .nav-link {
    &.active {
      color: var(--#{$variable-prefix}white);
      background-color: var(--#{$variable-prefix}danger);
      border-color: var(--#{$variable-prefix}danger);
      @if ($nav-tabs-shadow) {
        box-shadow: 0
          0.125rem
          0.25rem
          rgba(var(--#{$variable-prefix}danger-rgb), 0.3);
      }
    }
  }
  &.nav-slider {
    .nav-link {
      &.active {
        @if ($nav-tabs-shadow) {
          box-shadow: 0
            0.125rem
            0.25rem
            rgba(var(--#{$variable-prefix}danger-rgb), 0);
        }
      }
    }
  }
}

