.nav-slider {
  position: relative;
  .nav-link {
    &.active {
      background-color: transparent;
    }
  }
  .nav-item {
    z-index: 3;
  }
  .nav-slider-thumb {
    z-index: 1 !important;
    width: 100%;
    color: var(--#{$variable-prefix}danger);
    background: var(--#{$variable-prefix}danger);
  }
}
